.care {
  background-image: url('../img/background/Desktop/bgc-care.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 84px 50px;
  min-height: 645px;

  @media (max-width: $tablet-width) {
    padding-left: 0;
    padding-right: 0;
    padding-top: 91px;
    padding-bottom: 106px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 51px;
    padding-bottom: 60px;
  }

  // care__secondary-title
  &__secondary-title {
    text-align: center;
    margin-bottom: 48px;

    br {
      display: none;
    }

    @media (max-width: $tablet-width) {
      margin-bottom: 37px;
    }

    @media (max-width: $mobile-width) {
      margin-bottom: 32px;

      br {
        display: block;
      }
    }
  }
}